import React from "react";
import { useInView } from "react-intersection-observer";
import { useGetNotificationAllStudentMessage } from "../../../../hooks/member_tab/activity-api";
import { useSelector } from "react-redux";
import QvipleLoading from "../../../../Loader/QvipleLoading";
import EmptyInfo from "../../../../Loader/EmptyInfo";
import style from "../Activity/Activity.module.css";
import StudentMessageActivity from "../Activity/StudentMessageActivity";

const StudentMessage = () => {
  const [allActivity, setAllActivity] = React.useState([]);
  const [ref, inView] = useInView();
  const [state, setState] = React.useState(true);
  const [page, setPage] = React.useState(1);
  const ids = useSelector((state) => state.idChange);
  const [showingDataLoading, setShowingDataLoading] = React.useState(true);

  const {
    getNotificationAllStudentMessage,
    getNotificationAllStudentMessageRefetch,
  } = useGetNotificationAllStudentMessage({
    data: {
      uid: ids?.id,
      page: page,
      limit: 10,
    },
    skip: !ids?.id,
  });

  React.useEffect(() => {
    if (ids?.id) {
      setShowingDataLoading(true);
      getNotificationAllStudentMessageRefetch();
    }
  }, [ids?.id, page, getNotificationAllStudentMessageRefetch]);
  React.useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  React.useEffect(() => {
    if (getNotificationAllStudentMessage?.all_message) {
      const uniqueDublicate = [
        ...allActivity,
        ...getNotificationAllStudentMessage?.all_message,
      ];
      const uniqueRefind = [
        ...new Set(uniqueDublicate.map(JSON.stringify)),
      ].map(JSON.parse);
      setAllActivity(uniqueRefind);
      setShowingDataLoading(false);
    } else if (getNotificationAllStudentMessage?.all_message?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    //for next api is call or not dashboard
    if (getNotificationAllStudentMessage?.all_message.length === 10)
      setState(true);
    else setState(false);
  }, [getNotificationAllStudentMessage?.all_message]);
  return (
    <div className={style.activity_caontainer}>
      {allActivity?.map((data, index) =>
        allActivity?.length === index + 1 ? (
          <div ref={ref} key={index}>
            <StudentMessageActivity data={data} />
          </div>
        ) : (
          <StudentMessageActivity data={data} key={index} />
        )
      )}

      {showingDataLoading && <QvipleLoading />}
      {!showingDataLoading &&
        !getNotificationAllStudentMessage?.all_message?.length && (
          <EmptyInfo
            customStyleContainer={{
              marginTop: "5rem",
              height: "inherit",
            }}
            title={"No any message found"}
          />
        )}
    </div>
  );
};

export default StudentMessage;
